<template>
  <div id="blog-header">
    <ul>
        <li>
            <router-link to="/" exact>博客</router-link>
             <router-link to="/add" exact>写博客</router-link>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'BlogHeader',
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul{
    list-style-type: none;
    text-align: center;
    margin: 0;
}
li{
    display: inline-block;
    margin: 0 10px;
}
a{
    color:#fff;
    text-decoration: none;
    padding: 12px;
    border-radius: 5px;
}
#blog-header{
    background-color: crimson;
    padding: 30px 0;
    margin: 0 auto 40px;
    width: 600px; 
}
.router-link-active{
    background-color: rgba(255, 255, 255, 0.8);
    color: #444;
}
</style>



