<template>
  <div v-theme:column.cons="'narrow'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <div v-for="(blog,index) in filteredBlogs" :key="index" class="single-blog">
      <router-link v-bind:to="'/blog/' + blog.id">
        <h2 v-rainbow v-rain>{{blog.title | uppercase}}</h2>
      </router-link>
      <article>
        {{blog.content | snippet}}
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data () {
    return {
      blogs:[],
      search:""
    }
  },
  created() {
    this.$http.get("https://my-vue-d7ceb.firebaseio.com/posts.json")
      .then(function(data){
        return data.json();
        // this.blogs = data.body.slice(0,10);
      })
        .then(function(data){
          // console.log(data)
          var blogsArray =[];
          for(let key in data){
            // console.log(key);
            // console.log(data[key]);
            data[key].id=key;
            blogsArray.push(data[key]);
          }
          // console.log(blogsArray);
          this.blogs = blogsArray;
        })
  },
  computed: {
    filteredBlogs:function(){
      var search = this.search;
      return this.blogs.filter(function(value){
        return value.title.match(search.toLowerCase());
      });
      // return this.blogs.filter((blog) =>{
      //     return blog.title.match(this.search.toLowerCase());
      // })
    },
  },
  filters:{
    "uppercase":function (value) {
      return value.toUpperCase();
    },
    // uppercase(value){
    //   return value.toUpperCase();
    // }
  },
  directives:{
    "rain":{
      bind(el,binding,vnode){
        el.style.backgroundColor ='';
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#show-blogs{
  max-width: 800px;
  margin: 0 auto;
}
#show-blogs a{
  text-decoration: none;
}
#show-blogs input[type="text"]{
  width: 100%;
  box-sizing: border-box;
  padding: 8px;
}
.single-blog{
  box-sizing: border-box;
  padding: 20px;
  margin: 20px 0;
  background-color: #eeeeee;
}
</style>



