<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form action="" v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title">
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js
          <input type="checkbox" value="Vue.js" v-model="blog.categories">
        </label>
        <label>Node.js
          <input type="checkbox" value="Node.js" v-model="blog.categories">
        </label>
        <label>React.js
          <input type="checkbox" value="React.js" v-model="blog.categories">
        </label>
        <label>Angular4
          <input type="checkbox" value="Angular4.js" v-model="blog.categories">
        </label>
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="(author,index) in authors" :key="index">
          {{author}}
        </option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>
    <div v-if="submmited">
      <h3>你的博客发布成功</h3>
    </div>

    <div id="preview">
      <h2>博客总览</h2>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="(category,index) in blog.categories" :key="index">
          {{category}}
        </li>
      </ul>
      <p>作者是：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        categories:[],
        author:""
      },
      authors:["Hemiah","Henry","Bucky"],
      submmited:false
    }
  },
  methods:{
    post:function(){
      this.$http.post("https://my-vue-d7ceb.firebaseio.com/posts.json",
        this.blog)
        .then(function(data){
          console.log(data);
          this.submmited = true;
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}
textarea{
  height: 200px;
  resize: none;
}
#checkboxes label{
  display: inline-block;
  margin-top: 0;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #ffffff;
  border: 0;
  padding: 14px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}
#preview{
  padding: 10px 20px;
  border: 1px dotted #cccccc;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>



